import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    employees: [],
    totalElements: 0,
    isLoading: false,
};

export const employeesSlice = createSlice({
    name: "employees",
    initialState,
    reducers: {
        getAllEmployeesReducer: (state, action) => {
            state.employees = action.payload;
            state.totalElements = action.payload.totalElements
        },

        searchEmployeesReducer: (state, action) => {
            state.employees = action.payload;
            state.totalElements = action.payload.totalElements
        },

        addEmployeeReducer: (state, action) => {
            state.employees.push(action.payload);
        },

        editEmployeeReducer: (state, action) => {
            const { id, ...editEmployees } = action.payload;
            const employeeResult = state.employees.findIndex((item) => item.id === id);

            if (employeeResult) {
                state.employees[employeeResult] = {
                    ...state.employees[employeeResult],
                    ...editEmployees,
                };
            }
        },

        deleteEmployeeReducer: (state, action) => {
            state.employees = state.employees.filter(
                (item) => item.id !== action.payload
            );
            state.isLoading = !state.isLoading;
        },
    },
});

// Action creators are generated for each case reducer function
export const {
    getAllEmployeesReducer,
    searchEmployeesReducer,
    addEmployeeReducer,
    editEmployeeReducer,
    deleteEmployeeReducer,
} = employeesSlice.actions;

export default employeesSlice.reducer;
