import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    provinces: [],
    districtByProvinceId: [],
    isLoading: false,
};

export const provinceSlice = createSlice({
    name: "provinces",
    initialState,
    reducers: {
        getAllProvincesReducer: (state, action) => {
            state.provinces = action.payload;
        },

        getDistrictByProvinceIdReducer: (state, action) => {
            state.districtByProvinceId = action.payload;
        },

        searchProvincesReducer: (state, action) => {
            state.provinces = action.payload;
        },

        addProvinceReducer: (state, action) => {
            state.provinces.push(action.payload);
        },

        editProvinceReducer: (state, action) => {
            const { id, ...editProvinces } = action.payload;
            const provinceResult = state.provinces.findIndex((item) => item.id === id);

            if (provinceResult) {
                state.provinces[provinceResult] = {
                    ...state.provinces[provinceResult],
                    ...editProvinces,
                };
            }
        },

        deleteProvinceReducer: (state, action) => {
            state.provinces = state.provinces.filter(
                (item) => item.id !== action.payload
            );
            state.isLoading = !state.isLoading;
        },
    },
});

// Action creators are generated for each case reducer function
export const {
    getAllProvincesReducer,
    getDistrictByProvinceIdReducer,
    searchProvincesReducer,
    addProvinceReducer,
    editProvinceReducer,
    deleteProvinceReducer,
} = provinceSlice.actions;

export default provinceSlice.reducer;
