import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    wards: [],
    isLoading: false,
};

export const wardsSlice = createSlice({
    name: "wards",
    initialState,
    reducers: {
        getAllWardsReducer: (state, action) => {
            state.wards = action.payload;
        },

        searchWardsReducer: (state, action) => {
            state.wards = action.payload;
        },

        addWardReducer: (state, action) => {
            state.wards.push(action.payload);
        },

        editWardReducer: (state, action) => {
            const { id, ...editWard } = action.payload;
            const districtResult = state.wards.findIndex((item) => item.id === id);

            if (districtResult) {
                state.wards[districtResult] = {
                    ...state.wards[districtResult],
                    ...editWard,
                };
            }
        },

        deleteWardReducer: (state, action) => {
            state.districts = state.districts.filter(
                (item) => item.id !== action.payload
            );
            state.isLoading = !state.isLoading;
        },
    },
});

// Action creators are generated for each case reducer function
export const {
    getAllWardsReducer,
    searchWardsReducer,
    addWardReducer,
    editWardReducer,
    deleteWardReducer,
} = wardsSlice.actions;

export default wardsSlice.reducer;
