import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    districts: [],
    wardsByDistrictId: [],
    isLoading: false,
};

export const districtSlice = createSlice({
    name: "districts",
    initialState,
    reducers: {
        getAllDistrictsReducer: (state, action) => {
            state.districts = action.payload;
        },

        getWardsByDistrictIdReducer: (state, action) => {
            state.wardsByDistrictId = action.payload;
        },

        searchDistrictsReducer: (state, action) => {
            state.districts = action.payload;
        },

        addDistrictReducer: (state, action) => {
            state.districts.push(action.payload);
        },

        editDistrictReducer: (state, action) => {
            const { id, ...editDistricts } = action.payload;
            const districtResult = state.districts.findIndex((item) => item.id === id);

            if (districtResult) {
                state.districts[districtResult] = {
                    ...state.districts[districtResult],
                    ...editDistricts,
                };
            }
        },

        deleteDistrictReducer: (state, action) => {
            state.districts = state.districts.filter(
                (item) => item.id !== action.payload
            );
            state.isLoading = !state.isLoading;
        },
    },
});

// Action creators are generated for each case reducer function
export const {
    getAllDistrictsReducer,
    getWardsByDistrictIdReducer,
    searchDistrictsReducer,
    addDistrictReducer,
    editDistrictReducer,
    deleteDistrictReducer,
} = districtSlice.actions;

export default districtSlice.reducer;
